define("ember-search-likes/utils/get-params", ["exports"], function (exports) {
  exports.getParams = getParams;

  function getParams(url) {
    var listParams = {};

    url.replace(/([^=&]+)=([^&]*)/gi, function (m, key, value) {
      return listParams[key] = value;
    });

    return listParams;
  }
});